ReactDOM.render(
    <Router history={createHashHistory()}>
        <Switch>
            <Route path='/' exact component={MainPage}/>
            <Route path='/'>
            
                <Suspense 
                    fallback={ <div style={{marginTop:'5rem',textAlign:'center'}} role="status">
                                    <span className="spinner-grow text-primary"></span>
                                    <span className="sr-only">Loading...</span>
                                </div>}>
                    <Switch>
                        <Route path='/people/:id' component={lazy(()=>import('./component/user_single'))} />
                        <Route path='/paper' component={lazy(()=>import('./component/paper'))}/>
                        <Route path='/people' component={lazy(()=>import('./component/user'))}/>
                        <Route path='/project' component={lazy(()=>import('./component/project'))} />
                        <Route path='/patent' component={lazy(()=>import('./component/patent'))} />
                    </Switch>
                </Suspense>
                <footer className='d-flex justify-content-around'>
                    <div>
                        <h6>Contact us</h6>
                        <div>
                            <div>
                                <span>address</span>
                                <span>武汉大学本科生院楼六楼南619室</span>
                            </div>
                            <div>
                                <span>phone</span>
                                <span>13248201775</span>
                            </div>
                            <div>
                                <span>e-mail</span>
                                <span>3321794527@qq.com</span>
                            </div>
                        </div>
                    </div>
                    <div>
                        <h6>Related Connection</h6>
                        <a href="http://mmap.whu.edu.cn">MMAP</a>
                    </div>
                </footer>
            </Route>   
        </Switch>      
           
    </Router>, 
        document.getElementById('root'));

