import React from 'react';
import $ from 'jquery';
import {Link,withRouter} from 'react-router-dom';

//节流函数
function throttle(callBack, time) {
    let timer = null
    //timer状态要常驻内存，这里做了一个闭包
    return function() {
        if (!timer) {
            timer = setTimeout(() => {
                callBack()
                timer = null
            }, time)
        }
    }
}

class Header extends React.Component{
    constructor(props){
        super(props);
        this.state={
            scroll:false
        }
       this.handleScroll=this.handleScroll.bind(this);
    }

    componentDidMount(){
        //节流
        window.onscroll=throttle(this.handleScroll,50);
    }

    handleScroll(){
        //console.log("scroll");
        if($(window).scrollTop()>30){
            this.setState({
                scroll:true
            })
        }else{
            this.setState({
                scroll:false
            })
        }
    }
    
    render(){
        const router_list=[
            '',
            '/people',
            '/paper',
            '/project',
            '/patent'
        ],
              match_res=this.props.location.pathname.match(/\/[a-zA-Z_]+/);
        const current_router=match_res?match_res[0]:'';
        //console.log(match_res);
        return(
            <nav className={"navbar navbar-expand-lg "+(this.state.scroll?"bg-light navbar-scroll navbar-light":"navbar-dark bg-dark")}>
                <a className="navbar-brand" >
                    <img src="./img/log2.png" className='d-inline-block mr-2' alt='log' width='100' height='25'/>
                    视频编码组
                </a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>

                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto">
                        <li className={"nav-item "+(current_router===router_list[0]?"active":"")}>
                            <Link className="nav-link" to="/" >主页 <span className="sr-only">(current)</span></Link>
                        </li>
                        <li className={"nav-item "+(current_router===router_list[1]?"active":"")}>
                            <Link className="nav-link" to="/people">成员</Link>
                        </li>
                        <li className={"nav-item "+(current_router===router_list[2]?"active":"")}>
                            <Link className="nav-link" to="/paper">论文</Link>
                        </li>
                        <li className={"nav-item "+(current_router===router_list[3]?"active":"")}>
                            <Link className="nav-link" to="/project">项目</Link>
                        </li>
                        <li className={"nav-item "+(current_router===router_list[4]?"active":"")}>
                            <Link className="nav-link" to="/patent">专利</Link>
                        </li>
                        
                    </ul>
                
                </div>
            </nav>
        )
    }
}

export default withRouter(Header);